code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Bruno Ace";
  src: url(/static/media/BrunoAce-Regular.62f62c80.ttf);
}

:root {
  --main-font-color: #ffffff;
  --drk-red: #4c2e34;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  position: relative;
  background: linear-gradient(165.89deg, #2a3646 -9.08%, #4c2e34 130.86%);
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Bruno Ace";
  font-size: 1em;
  padding: 45px 0px 45px 0px;
}

.app-container {
  display: flex;
  flex-flow: column wrap;
  position: relative;
  width: 90%;
  height: 100%;
  /* border: 1px solid white; */
}

#error-page {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
}

.section-height-controller {
  height: 920px;
}

#arrow-up-square {
  color: white;
  position: fixed;
  left: 96%;
  top: 93%;
  z-index: 3;
  cursor: pointer;
}

#arrow-up-square:hover {
  /* animation: name duration timing-function delay iteration-count direction fill-mode;; */
  -webkit-animation: pulse 0.7s ease-in;
          animation: pulse 0.7s ease-in;
}

.pulse {
  -webkit-animation: pulse 0.7s ease-in;
          animation: pulse 0.7s ease-in;
}

#skills-section,
#about-section,
#contact-section {
  height: auto;
  margin: 7% 0%;
}

#hero {
  height: 600px;
  margin: 5% 0% 0% 0%;
}

.skill,
#about-me-p {
  background: linear-gradient(45deg, #4a2323, transparent);
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

