@media screen and (max-width: 1453px) {
  #type-of-dev {
    font-size: 2.889em;
  }
  #strapline {
    top: 35px;
    font-size: 0.875em;
  }
}

@media only screen and (min-width: 1081px) and (max-width: 1206px) {
  #type-of-dev {
    margin: 45px 0px 45px -3px;
    line-height: 50px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1080px) {
  #type-of-dev {
    font-size: 2.5em;
    line-height: 50px;
    margin: 45px 0px 45px -3px;
  }
}

@media only screen and (max-width: 1024px) {
  #arrow-up-square {
    display: none !important;
  }
  #about-section {
    justify-content: center;
  }

  #about-me-section {
    width: inherit;
  }
}

@media only screen and (min-width: 320px) and (max-width: 768px) {
  /** Header Section*/

  /** Hero Section*/

  header {
    margin-top: -30px;
  }

  #hero {
    margin: 0px;
  }

  #hero #intro span.hi-light {
    font-size: 1.2em;
  }

  #type-of-dev {
    font-size: 2em;
    flex-flow: row wrap;
    margin: 40px 0px 60px 0px;
    line-height: 35px;
  }

  #hero_container {
    margin-top: 0px;
  }

  #more-about-me {
    justify-content: center;
  }

  #more-about-me-button {
    margin-right: 0;
  }

  /** Skills Section */
  .skill {
    width: auto !important;
  }
  #skills-container {
    width: 100%;
  }

  #skills-title-section {
    justify-content: center;
    margin-top: 30px;
  }

  #skills-title-section * {
    font-size: 50px;
    height: 150px;
  }
  #skill-container {
    flex-flow: column wrap;
  }
  #skill-languages,
  #skill-studying {
    min-width: 0px;
    padding-bottom: 100px;
  }

  /** About Section */
  #about-container {
    align-content: center;
    width: 100%;
  }
  .about-title-section-main {
    flex-flow: column wrap;
    flex: 1 1 auto;
    align-self: center;
  }
  #about-me-p {
    flex: 1 1 auto;
    min-width: 0px;
    font-size: 16px;

    margin-left: 0px;
    width: auto;
    min-width: 0px;
  }
  #about-me-p span {
    font-size: inherit;
  }
  #about-title-section * {
    font-size: 50px;
    height: 150px;
    margin-top: 30px;
  }
  #about-title {
    margin-top: 0px;
  }

  /** Contact Section */

  #contact-section {
    margin-top: 30px;
    justify-content: center;
  }
  #contact-title-section {
    justify-content: center;
  }
  #contact-title-section * {
    font-size: 49px;
    height: 150px;
    justify-content: center;
  }
  #nates-details {
    display: flex;
    flex-flow: column wrap;
    font-size: 0.4em;
  }
  #contact-socials {
    align-self: center;
    margin-left: 20px;
  }
  /** Footer */
  footer {
    width: 100%;
  }
  #footer-copy {
    font-size: 14px;
    align-items: center;
  }

  /** Other */
  #sideways-subject {
    display: none;
  }

  span.hi-light {
    font-size: 10px;
  }

  #right-arrow {
    display: none;
  }
}

/* Custum */
@media only screen and (min-width: 320px) and (max-width: 1204px) {
  #hero #intro {
    max-width: 100%;
  }
}

@media only screen and (min-width: 320px) and (max-width: 375px) {
  #skills-section {
    margin-top: 30%;
  }
}

/****************Custum****************************/

.pls {
  position: absolute;
  width: 195px;
  height: 70px;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  border-radius: 7px;
}

.pls::before,
.pls::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: inherit;
  z-index: -1;
  opacity: 0.7;
  border-radius: 7px;
}

.pls::before {
  -webkit-animation: p 1s ease-in infinite;
          animation: p 1s ease-in infinite;
}

/* .pls::after {
  animation: p 1.2s ease-out infinite;
} */

@-webkit-keyframes p {
  100% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
  }
}

@keyframes p {
  100% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    opacity: 0;
  }
}

header {
  display: flex;
  justify-content: space-between;
  color: var(--main-font-color);
  font-family: "Bruno Ace";
  width: 100%;
}

.h-container {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.name-logo {
  text-transform: uppercase;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 250px;
}

.nav-link {
  transition: color 0.3s, -webkit-filter 0.3s 0.2s;
  transition: color 0.3s, filter 0.3s 0.2s;
  transition: color 0.3s, filter 0.3s 0.2s, -webkit-filter 0.3s 0.2s;
}
.nav-link:hover {
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 30px rgb(201, 122, 138));
          filter: drop-shadow(0 0 30px rgb(201, 122, 138));
  color: rgb(201, 122, 138);
}

#cor {
  width: 82px;

  /* font-family: "Rajdhani"; */
  font-style: normal;
  font-weight: 400;
  font-size: 37px;
  line-height: 51px;
  text-align: center;
  letter-spacing: 0.04em;
}

#bitt {
  position: relative;
  width: 91px;
  top: -15px;
  /* font-family: "Rajdhani"; */
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 51px;
  text-align: center;
  letter-spacing: 0.17em;
}

:root {
  --drk-hi-light: rgb(152 67 84);
}

.hi-light {
  color: #ff6c8a;
  text-shadow: 0px 0px 10px #ff6c8a;
  letter-spacing: 1px;
}

.icon {
  width: 76px;
  z-index: 1;
  position: relative;
  width: 50px;
}

.transition {
  transition: box-shadow 0.5s ease;
}

.flex_center {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.flex_center_column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.skill {
  box-shadow: 14px 14px black;
}
.skill:hover {
  box-shadow: 14px 14px 24px black;
}

#sql-nosql {
  font-weight: bold;
  border-radius: 7px;
  border: 2px solid white;
  font-size: 0.6em;
}

.min-width-studying {
  min-width: 290px;
}
.min-width-lang {
  min-width: 312px;
}

textarea {
  padding: 12px;
}

input {
  padding: 12px 12px 12px 30px;
}

.contact-strapline {
  font-size: 1.2em;
  color: grey;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.contact-headline {
  font-size: 2em;
}
#contact-email {
  font-size: 4em;
}
.contact-section-hr {
  margin: 30px 0px 30px;
}

